
/* Navigation Styling */
.navbar {
  position: fixed !important;
  width: 100%;
  padding: 10px;
  z-index: 100;
  transition: 0.3s;
  opacity: 0.5;
}

.navbar-logo {
  width: 30vh;
  height: auto;
  padding-left: 15px;
}

.link_bundle {
  margin-left: 30px;
}

.scrolling-active {
  transition: 0.5s;
  opacity: 1;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

/* Homepage Styling */
.homeImgContainer {
  width: 100%;
  height: 100vh;
  z-index: 1;
}

.topImg {
  background-image: url('./images/2D07064DE8654E42B78FD162B34CC5E7.webp');
  position: absolute;
  width: 100%;
  height: 100vh;
  max-height: 100vh;
  filter: brightness(0.7);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.headerOneText {
  top: 50vh;
  width: 100%;
  font-size: 50px;
  position: absolute;
  color: white;
  z-index: 1;
  text-align: center;
  letter-spacing: 6px;
  text-transform: uppercase;
}

/* about */
